<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import MarkdownIt from 'markdown-it'
import { db, ensureAnonLogin, auth, ts } from './lib/firebase'
import {
  collection, addDoc, deleteDoc, doc,
  onSnapshot, query, orderBy, getDocs
} from 'firebase/firestore'
import { onAuthStateChanged } from 'firebase/auth'

const md = new MarkdownIt({ breaks: true })

/* =============== Â∑¶„Çµ„Ç§„ÉâÔºöMarkdown „É≠„Éº„Éâ & Êú¨ÊñáÔºàÂÆâÂÖ®ÁâàÔºâ =============== */
const mdFiles = import.meta.glob('./content/*.md', {
  query: '?raw',
  import: 'default',
  eager: true,           // ‚Üê ÊñáÂ≠óÂàó„ÇíÁõ¥Êé•Âèó„ÅëÂèñ„Çã
})

const topics = ref([])
const selectedId = ref(null)
const selectedHtml = ref('')

function loadTopics() {
  const items = []
  for (const [path, raw] of Object.entries(mdFiles)) {
    if (typeof raw !== 'string') continue
    const firstLine = raw.split(/\r?\n/).find(l => l.trim()) || ''
    const title = firstLine.replace(/^#\s*/, '') || '(ÁÑ°È°å)'
    const id = path.split('/').pop().replace(/\.md$/, '')
    items.push({ id, title, raw })
  }
  items.sort((a, b) => a.id.localeCompare(b.id, 'ja'))
  topics.value = items
  if (!selectedId.value && items.length) selectedId.value = items[0].id
}

watch(selectedId, () => {
  const t = topics.value.find(x => x.id === selectedId.value)
  selectedHtml.value = t ? md.render(t.raw) : ''
})

/* =============== Âè≥Ôºö„É°„É≥„Éê„ÉºÊäïÁ®øÔºàFirestoreÂÖ±ÊúâÔºâ =============== */
const MAX_CHARS = 500
const MAX_LINES = 10

const nameInput = ref('')
const newMessage = ref('')
const messages   = ref([])
const me         = ref(null)
const hasFirebase = ref(false)    // ‚Üê FirebaseÊúâÂäπ„Åã„Å©„ÅÜ„Åã
const SHOW_FB_NOTICE = false      // ‚Üê ‰∏ÄÊôÇÁöÑ„Å´„ÄåÊú™Ë®≠ÂÆö„Éê„Éä„Éº„Äç„ÇíÈö†„Åô„Å™„Çâ false „Å´Âõ∫ÂÆö

let colRef = null

const charCount = computed(() => newMessage.value.length)
const lineCount = computed(() => newMessage.value ? newMessage.value.split(/\r\n|\r|\n/).length : 0)
const isCharOver = computed(() => charCount.value > MAX_CHARS)
const isLineOver = computed(() => lineCount.value > MAX_LINES)
const canSubmit  = computed(() => !!newMessage.value.trim() && !isCharOver.value && !isLineOver.value)

onMounted(async () => {
  loadTopics()

  // Firebase „ÅåÊúâÂäπ„Å™„Å®„Åç„Å†„ÅëË™çË®ºÔºÜË≥ºË™≠
  if (db) {
    hasFirebase.value = true
    const user = await ensureAnonLogin()
    me.value = user
    if (auth) onAuthStateChanged(auth, (u) => { me.value = u })

    colRef = collection(db, 'messages')
    const q = query(colRef, orderBy('created_at', 'desc'))
    onSnapshot(q, (snap) => {
      messages.value = snap.docs.map(d => {
        const data = d.data()
        const dt = data.created_at?.toDate ? data.created_at.toDate() : new Date()
        return {
          id: d.id,
          name: data.name || 'ÂêçÁÑ°„Åó',
          text: data.text || '',
          at: dt.toLocaleString(),
          uid: data.uid ?? null,
        }
      })
    })
  }
})

function needFirebase() {
  if (!hasFirebase.value || !colRef) {
    alert('FirebaseÊú™Ë®≠ÂÆöÔºà.env.local „Çí‰Ωú„Å£„Å¶„Éì„É´„Éâ„Åô„Çã„Å®ÊäïÁ®ø„Åß„Åç„Çã„Åπ„ÅïÔºâ')
    return true
  }
  return false
}

function handleSubmit(){
  if (needFirebase()) return
  const t = newMessage.value.trim()
  const nm = nameInput.value.trim() || 'ÂêçÁÑ°„Åó'
  if(!canSubmit.value) return
  addDoc(colRef, {
    name: nm,
    text: t,
    created_at: ts(),
    uid: me.value?.uid ?? null,
  }).then(() => {
    newMessage.value = ''
  }).catch(e => console.error('[addDoc] error', e))
}

async function removeOne(id){
  if (needFirebase()) return
  try {
    await deleteDoc(doc(db, 'messages', id))
  } catch (e) {
    console.error('[delete] error', e)
    alert('ÂâäÈô§„Å´Â§±Êïó„Åó„Åü„Åã„ÇÇ„ÄÇ')
  }
}

async function clearAll(){
  if (needFirebase()) return
  if(!confirm('ÂÖ®ÊäïÁ®ø„ÇíÂâäÈô§„Åó„Åæ„Åô„ÅãÔºü')) return
  const snap = await getDocs(colRef)
  for (const d of snap.docs) await deleteDoc(d.ref)
}
</script>

<template>
  <div class="layout-pc">
    <!-- Â∑¶Ôºö„Çµ„Ç§„Éâ„Éä„Éì -->
    <aside class="sidenav">
      <div class="sidenav-inner">
        <div class="logo">MENU</div>
        <nav aria-label="„Çµ„Ç§„Éâ„Éä„Éì">
          <ul class="nav-list">
            <li v-for="t in topics" :key="t.id">
              <a href="#" class="nav-link" :class="{active: t.id===selectedId}"
                 @click.prevent="selectedId = t.id" :aria-current="t.id===selectedId ? 'page' : undefined">
                <span class="dot" aria-hidden="true"></span>
                <span class="nav-text">{{ t.title }}</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </aside>

    <!-- ‰∏≠Â§ÆÊú¨Êñá -->
    <main class="content">
      <h2 class="title">‚ô†‚ô°‚ô¶‚ôß RoR„É°„É≥„Éê„Éº„Çµ„Ç§„Éà ‚ô†‚ô°‚ô¶‚ôß</h2>
      <p class="lead">„Åì„Åì„Åß„ÅØ„É°„É≥„Éê„ÉºÂêë„Åë„Å´ <strong>„É´„Éº„É´</strong> „ÇÑ <strong>„Éé„Ç¶„Éè„Ç¶</strong> „ÇíÂÖ±Êúâ„Åó„Åæ„Åô„ÄÇ</p>

      <section class="panel">
        <template v-if="selectedHtml">
          <article class="md" v-html="selectedHtml"></article>
        </template>
        <template v-else>
          <div class="loading">Ë™≠„ÅøËæº„Åø‰∏≠‚Ä¶Ôºàcontent „Éï„Ç©„É´„ÉÄ„Å´ .md „ÇíÁΩÆ„Åè„Å®Ëá™Âãï„ÅßÂá∫„Çã„ÅπÔºâ</div>
        </template>
      </section>
    </main>

    <!-- Âè≥ÔºöÊäïÁ®ø -->
    <aside class="sidebar">
      <div class="pad">
        <h3>‚úçÔ∏è „É°„É≥„Éê„ÉºÊõ∏„ÅçËæº„Åø</h3>

        <!-- ‰∏ÄÊôÇÁöÑ„Å´„Éê„Éä„Éº„ÇíÈö†„Åó„Åü„ÅÑ„Å®„Åç„ÅØ SHOW_FB_NOTICE=false „Å´„Åó„Å¶„ÅÇ„Çã -->
        <div v-if="SHOW_FB_NOTICE && !hasFirebase" class="card" style="border-color:#f8cdcd;background:#fff5f5">
          <strong style="color:#b20000">FirebaseÊú™Ë®≠ÂÆö</strong><br>
          `.env.local` „Çí‰Ωú„Å£„Å¶„Éì„É´„Éâ„Åô„Çã„Å®ÊäïÁ®ø„Åß„Åç„Çã„Åπ„ÅïÔºà‰∏ã„Å´ÊâãÈ†ÜÊõ∏„ÅÑ„ÅüÔºâ„ÄÇ
        </div>

        <form @submit.prevent="handleSubmit" class="card" :aria-disabled="!hasFirebase">
          <label class="lbl">„Éã„ÉÉ„ÇØ„Éç„Éº„É†</label>
          <input v-model="nameInput" class="input" placeholder="‰æãÔºöMomo56 / ÂêçÁÑ°„Åó „Å™„Å©" :disabled="!hasFirebase" />

          <label class="lbl">„É°„ÉÉ„Çª„Éº„Ç∏</label>
          <textarea v-model="newMessage" class="textarea" placeholder="Ë§áÊï∞Ë°åOK„ÄÇ„Éé„Ç¶„Éè„Ç¶„ÇÑÈÄ£Áµ°‰∫ãÈ†Ö„Å™„Å©„Çí„Å©„ÅÜ„Åû" rows="5" :disabled="!hasFirebase"></textarea>

          <div class="hint">
            <span :class="{over:isCharOver}">{{ charCount }}/{{ MAX_CHARS }}ÊñáÂ≠ó</span><span>„Éª</span>
            <span :class="{over:isLineOver}">{{ lineCount }}/{{ MAX_LINES }}Ë°å</span>
          </div>

          <div class="row">
            <button class="btn-primary" type="submit" :disabled="!canSubmit || !hasFirebase">ÊäïÁ®ø</button>
            <button class="btn-danger" type="button" @click="clearAll" :disabled="!hasFirebase">ÂÖ®ÂâäÈô§</button>
          </div>
        </form>

        <div class="card list">
          <h4>üóÇ ÊúÄÊñ∞„ÅÆÊäïÁ®ø</h4>
          <ul class="posts">
            <li v-for="m in messages" :key="m.id" class="post">
              <div class="meta">
                <strong class="name">{{ m.name || 'ÂêçÁÑ°„Åó' }}</strong>
                <small class="time">{{ m.at }}</small>
                <button class="btn-mini" type="button" v-if="hasFirebase && m.uid && me && m.uid === me.uid" @click="removeOne(m.id)">
                  ÂâäÈô§
                </button>
              </div>
              <div class="text" v-text="m.text"></div>
            </li>
            <li v-if="messages.length===0" class="empty">„Åæ„Å†ÊäïÁ®ø„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ</li>
          </ul>
        </div>

        <p class="note">‚Äª Firestore„Åß„É™„Ç¢„É´„Çø„Ç§„É†ÂÖ±Êúâ„Åó„Å¶„ÅÑ„Åæ„Åô„ÄÇ</p>
      </div>
    </aside>
  </div>
</template>

<style>
