  border:1px solid #e9e9e9; border-radius: 10px; padding: 10px 12px; transition: background .15s, border-color .15s, box-shadow .15s; }
.nav-link .dot{ width:8px; height:8px; border-radius:50%; background:#d0d0d0; }
